import { Component, OnInit } from '@angular/core';
import { SwPush, SwUpdate } from '@angular/service-worker';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  subscriptionDetails = 'Not yet subscribed !'
  VAPID_PUBLIC_KEY = "BGVVbWxcDH1nNQgbFJCM_EZNSNd3MQbf1tCXPXSJ6zkdwHwHmUeLgJZDMoxADD9rVnjwEQ3NY-hcivbApG0a10U";

  constructor(
    private swUpdate: SwUpdate,
    private swPush: SwPush
  ) { }


  subscribeToNotifications() {
    this.swPush.requestSubscription({
      serverPublicKey: this.VAPID_PUBLIC_KEY
    }).then(sub => {
      console.error("Notification Subcription Success !!!", sub);
      this.subscriptionDetails = JSON.stringify(sub);
    }).catch(err => {
      console.error("Could not subscribe to notifications", err)
    });
  }

  ngOnInit() {
    if (this.swUpdate.isEnabled) {
      this.swUpdate.available.subscribe(() => {
        if (confirm("New version available. Load New Version?")) {
          window.location.reload();
        }
      });
    }
  }

}
